#lcut-window {
  z-index: 2;
  background: #fff;
  width: 80%;
  height: 80%;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 4px 10px #0003;
}

#lcut-preview {
  background-color: #f5f5f5;
  border: 1px solid #ccc;
  width: 100%;
  height: 60%;
  overflow-x: scroll;
}

.lcut-ui-btn {
  cursor: pointer;
  background-color: #f0f0f0;
  border: 1px solid #333;
  width: auto;
  margin-right: 10px;
  padding: 10px 20px;
  display: inline-block;
}

